import React from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

const MenuContentsItem = props => {
  return (
    // <div className="seoulContainer-m popup-box">
    <div className="popup-box">
      <button
        className="close"
        onClick={() => {
          // props.history.goBack();
          let array = props.history.location.pathname.split('/');
          array.pop();
          array = array.join('/');
          props.history.push(array);
        }}
      >
        &times;
      </button>
      <div className="contentBox">
        <h1 className="title">
          {props.item.name} <span className="price">${props.item.price}</span>
        </h1>
        <div className="img-box">
          <img
            src={props.item.largeImage}
            alt={props.item.name}
            title={props.item.name}
          />
        </div>
        <p className="desc">{props.item.description}</p>
      </div>
    </div>
  );
};
MenuContentsItem.propTypes = {
  item: PropTypes.object.isRequired
};

export default withRouter(MenuContentsItem);
